$princepal-color:rgba(0,136,169,1);
$back-color: #242528;

@import "fonctions";

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: $back-color;
}

.container{


    &-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 10%;

        &-logo{
            @extend %logo_style;

        }

        &-nav_bar{
            list-style: none;

            li{
                display: inline-block;
                padding: 0px 20px;

                a{
                    transition: all 0.3s ease 0s;
                    text-decoration: none;
                    color: #fff;

                    &:hover{
                        @extend %logo_style;
                    }
                }
            }
        }

            &-button{
                padding: 9px 25px;
                background-color: $princepal-color;
                border: none;
                border-radius: 50px;
                cursor: pointer;
                transition: all 0.3s ease 0s;
                text-decoration: none;
                color: #fff;

                &:hover{
                    background-color: rgba(0,136,169,0.8);
                }
            }

    }

    &-content{

        color: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 30px 10%;  

        &-decoration{

            

            a{
                text-decoration: none;
                color: #fff;
            }


        }



    }

}